	module.exports = {"data":{"post":{"html":"<h2>Today I Made Some New Additions</h2>\n<p>The portfolio hasn't seen an update in at least a few months, so I figured it's about time I made an effort to make it reflect what I've actually been doing lately. So today I added two new items:</p>\n<ol>\n<li>OrangeFinders.com</li>\n<li>Creative Words Theme</li>\n</ol>\n<p>The Creative Words Theme is something I threw together this last weekend. I went through a Photoshop tutorial to create the design, then decided to test my new WP skills by coding it up clean and fast. Said new WP skills come from the former item on that list: OrangeFinders.com</p>\n<!--more-->\n<h2>My First Full Web App</h2>\n<p>OrangeFinders.com marks my first fully featured web app created. I say fully featured in full knowledge of the fact that there are still a few minor changes that I need to make, and that the ‘wish list' of features for the site is still quite long.</p>\n<p>But that's beside the point. This site taught me a plethora of new skills that are highly relevent to web design and WP development. This include: Form Validation (soooo boring), AJAX (super cool) and general PHP awesomeness. Having largely completed the functionality of that site I'm now fully confident standing by the ‘developer' half of my job. I now feel capable of doing just about anything I need to for a client with the combination of PHP and WP. Consequently, I've reshifted my focus back to the design half of my job. I'm going to learn Illustrator, create a logo for myself and most importantly completely redesign my site from the ground up. When? Good question, ideally before my birthday next month.</p>\n<p>I'm still a long ways from where I want to be in terms of personal knowledge on both the design and development fronts, but I'm quite content that way. Having myriad new fields that I intend to learn about is much more exciting than it is daunting. Not time to waste, I'm getting started right after I finish this post.</p>","frontmatter":{"created":"2013-05-14T07:00:00.000Z","title":"Portfolio Update + Future Site Update"}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2013-05-14-portfolio-update-future-site-update.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"Becoming A Developer Is A Process","created":"2013-05-11T07:00:00.000Z"},"slug":"becoming-a-developer-is-a-process"},"prev":{"frontmatter":{"title":"WordPress Development Server - Full Set Up Guide","created":"2013-05-30T07:00:00.000Z"},"slug":"wordpress-development-server-full-set-up-guide"}}}

